.background {
	height: 100%;
	width: 100%;
	z-index: 0;
	overflow: hidden;
}

.container {
	display: -webkit-box;
   	display: -ms-flexbox;
   	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	min-height: 22rem;
	height: 100vh;
    width: auto;
	margin: 0 auto;
	background-color: transparent;

	@include bp(small) {
		margin: 0 auto;
	}
	
	.container__inner {
		width: 100%;
	   	height: 100%;
	   	display: -webkit-box;
	   	display: -ms-flexbox;
		position: relative;
	   	display: flex;
	   	-webkit-box-orient: vertical;
	   	-webkit-box-direction: normal;
	       	-ms-flex-flow: column;
	           	flex-flow: column;
	   	// -webkit-box-pack: center;
	    //    	-ms-flex-pack: center;
	    //        	justify-content: center;
	   	-webkit-box-align: center;
	       	-ms-flex-align: center;
	           	align-items: center;
			

		@include bp(small) {
			width: 95%;
			height: 85%;

		}
		@include bp(medsmall) {

		}

		@include bp(large) {
			width: 37.5rem;
			height: 37.5rem;
			z-index: 3;
		}
	}

	.container__inner-top {
		position: absolute;
		max-width: 15rem;
    	max-height: 23rem;
    	height: 23rem;
    	margin: 1% auto;
		opacity: 1;
		z-index: 10;
		display: -webkit-box;
	   	display: -ms-flexbox;
	   	display: flex;
	   	-webkit-box-orient: vertical;
	   	-webkit-box-direction: normal;
	       	-ms-flex-flow: column;
	           	flex-flow: column;
		align-items: flex-start;
		justify-content: center;

		-webkit-transition:  opacity 0.5s ease-out, z-index 0.5s ease-out;
		-moz-transition:  opacity 0.5s ease-out, z-index 0.5s ease-out;
		-o-transition: opacity 0.5s ease-out, z-index 0.5s ease-out;
		transition:  opacity 0.5s ease-out, z-index 0.5s ease-out;

		@include bp(small) {
			max-width: 17rem;
    		
		}
		@include bp(medsmall) {
			max-width: 50rem;
			justify-content: flex-start;

		}

		.question {
			-ms-flex-item-align: center;
			-ms-grid-column-align: center;
			flex: 1 0 auto; 
			align-self: center;
			text-align: left;
			padding: 5px;
			margin: 0 auto 0.7rem auto;
			font-size: 3.5rem;

			@include bp(medsmall) {
				width: 100%;
				flex: 0 1 auto;
				text-align: center;
			} 
		}
	
		button {
			-ms-flex-item-align: center;
			-ms-grid-column-align: center;
			align-self: center;
			flex: 1 0 auto;
			width: 100%;
			min-height: 3.5rem;
			border: 2px solid $c-cond-cool;
			border-radius: 0.3125rem;
			background-color: $c-white;
			font-family: 'Varela Round', sans-serif;
			font-size: 1rem;
			color: $c-text;
			baseline-shift: baseline;

			&:focus {
				outline: none;
				border: none;
			}
			&:hover {
				background-color: $c-cond-cool;
			}

			@include bp(small) {
				width: 100%;
			}
			
			@include bp(medsmall) {
				flex: 0 1 auto;
			}
		}
	}

	.container__inner-bottom {
		position: absolute;
	 	max-width: 15.75rem;
	 	max-height: 15.75rem;
	 	width: 100%;
	 	height: 100%;
	 	min-height: 25rem;
	 	margin: auto;
	 	position: relative;
	 	top: 0;
	 	background-color: transparent;
	 	z-index: 2;

		.circle {	
			margin: 0 auto;
			display: block;
			border-radius: 50%;
			transform: scale(1);
		}

		@include bp(small) {
	 		max-width: 28.5rem;
	 		max-height: 28.5rem;
		}

		@include bp(medsmall) {
	 		max-width: 28.5rem;
	 		max-height: 28.5rem;
		}

		.circle__inner {
			position: absolute;
			width: 100%;
			padding: 0.3125rem;
			text-align: center;
			font-size: 1.2rem;
		}

		.circle__inner_con {
			top: 36.5%;

			@include bp(small) {
				top: 35.5%;
			}

			.conditions {
				display: block;
				margin: auto;
				height: 100%;
				width: 100%;
				text-transform: capitalize;

				@include bp(small) {
					font-size: 1.5rem;
				}
			}
		}

		.circle__inner_temp {
			top: 36%;

			.temperature {
				display: block;
				letter-spacing: 0.075rem;
			}
		}

		.circle__inner_clock{
			top: 26% ;

			@include bp(medium) {
				top: 30%;
			}

			.message {
				display: block;
			}
		}
	}
 }

.circleCon {
	display: block;
	opacity: 0;
    height: 8rem;
    width: 8rem;
    position: absolute;
 	left: 34%;
  	top: 0;
  	transform: translate(-50%, -50%);
    transform: scale(1);
	background: $c-transparent;
	transition: opacity 0.5s;
	-webkit-transition: display 1s ease ;
	-moz-transition: display 1s ease ;
	-o-transition: display 1s ease ;
	transition: display 1s ease ;

	@include bp(small) {
		min-height: 10rem;
		min-width: 10rem;
    	left: 38%;
	    top: -6%;
	}

	@include bp(medium) {
		top: 20%;
		left: -9%;
	}
}

.circleTemp {
	display: block;
	height: 5.8125rem;
    width: 5.8125rem;
    position: absolute;
	left: 55%;
    top: 30%;
  	transform: translate(-50%, -50%);
	font-family: $font-accent;
	background: $c-transparent;
	
	@include bp(small) {
		min-width: 7.8125rem;
		min-height: 7.8125rem;
		left: 58%;
		top: 26%;
	}

	@include bp(medium) {
		left: 25%;
    	top: 19%;
	}
}

.circleClock {
	display: block;
	opacity: 0;
	height: 15.75rem;
	width: 15.75rem;
    position: absolute;
	left: 0;
  	top: 39%;
  	transform: translate(-50%, -50%);
	background: $c-transparent;
	transition: opacity 2.5s;

	@include bp(small) {
		height: 18.75rem;
		width: 18.75rem;
		left: 10%;
		top: 41%;
	}
	
	@include bp(medium) {
		left: 46%;
		top: 26%;
	}
}